@import 'components/variables';

.section-welcome-admin {
  width: 100%;
  height: 100px;
  background-image: url(../images/bg-back.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.section-admin {
  .sidebar {
    border-width: 3px;
    border-style: solid;
    border-image: radial-gradient(circle, #9CD3CD 0%, #BDE7E2 34%, #FFFFFF 94%) 1 100%;
    a {
      color: $gray-light;
      font-weight: 300;
      text-decoration: none;
      transition: all 0.3s;
      &:hover {
        font-weight: 600;
      }
      &.active {
        font-weight: 600;
      }
      p {
        margin-bottom: 2.5rem;
      }
    }
  }
  .content {
    .title {
      font-weight: 600;
      font-size: 1.2rem;
      color: $blue;
    }
    .table-perfil {
      tbody {
        tr {
          th {
            font-weight: 300;
            padding-right: 2rem;
            padding-bottom: 1rem;
          }
          td {
            font-weight: 400;
            padding-bottom: 1rem;
          }
        }
      }
    }
  }
}